import React from "react";
import type { MenuProps } from "antd";
import { Layout, Menu } from "antd";
import RouterViewHome, { rList } from "../router/RouterViewHome";
import { useLocation } from "react-router-dom";
const { Header, Content, Sider } = Layout;

const items: MenuProps["items"] = rList.map((item) => {
  return {
    key: `${item.id}`,
    icon: React.createElement(item.icon),
    // label: `subnav ${key}`,
    label: <a href={"#/home" + item.path}>{item.msg}</a>,

    // children: new Array(4).fill(null).map((_, j) => {
    //   const subKey = index * 4 + j + 1;
    //   return {
    //     key: subKey,
    //     label: `option${subKey}`,
    //   };
    // }),
  };
});

const Home = () => {
  let location = useLocation();
  let key = String(
    rList.filter((item) => {
      return "/home" + item.path === location.pathname;
    })[0]?.id
  );
  return (
    <Layout style={{ height: "100%" }}>
      <Header className="header" style={{ backgroundColor: "#5ec0fe" }}>
        <h1 style={{ textAlign: "center" }}>27</h1>
      </Header>
      <Layout>
        <Sider width={200} className="site-layout-background">
          <Menu
            mode="inline"
            selectedKeys={[key]}
            // defaultSelectedKeys={["1"]}
            // defaultOpenKeys={["sub1"]}
            style={{ height: "100%", borderRight: 0 }}
            items={items}
          />
        </Sider>
        <Layout style={{ padding: "0 6px 6px"}}>
          <Content
            className="site-layout-background"
            style={{
              padding: 24,
              margin: 0,
              minHeight: 280,
            }}
          >
            <RouterViewHome></RouterViewHome>
          </Content>
        </Layout>
      </Layout>
    </Layout>
  );
};

export default Home;
