import { createSlice } from "@reduxjs/toolkit";
import partaModel from "../model/partaModel";
import partbModel from "../model/partbModel";
import orderModel from "../model/orderModel"
import {
  productModel,
  productColorModel,
  productDirectionModel,
  productLightModel,
} from "../model/productModel";
const initialState = {
  partaList: [],
  partbList: [],
  productList: [],
  productColorList: [],
  productDirectionList: [],
  productLightList: [],
  orderList: [],
};
export enum initPayload {
  partaList = "partaList",
  partbList = "partbList",
  productList = "productList",
  productColorList = "productColorList",
  productDirectionList = "productDirectionList",
  productLightList = "productLightList",
  orderList = "orderList",
}
const infoSlice = createSlice({
  name: "info",
  initialState: initialState,
  reducers: {
    initList(state, action) {
      return Object.assign(state, {
        [action.payload.attr]: action.payload.list,
      });
    },
    addList(state, action) {
      return Object.assign(state, {
        [action.payload.attr]: [
          ...state[action.payload.attr as initPayload],
          action.payload.item,
        ],
      });
    },
    addOrderList(state, action){
      return Object.assign(state, {
        [action.payload.attr]: [
          ...state[action.payload.attr as initPayload],
          ...action.payload.list,
        ],
      });
    },
    delList: (state, action) => {
      return {
        ...state,
        [action.payload.attr]: state[action.payload.attr as initPayload].filter(
          (item: any) => {
            return item._id !== action.payload._id;
          }
        ),
      };
    },
  },
});

export const { initList, addList, delList } = infoSlice.actions;

export default infoSlice.reducer;

export const asyncInitList = (payload: any) => async (dispatch: any) => {
  if (payload === initPayload.partaList) {
    var { data } = await partaModel.list();
  } else if (payload === initPayload.partbList) {
    var { data } = await partbModel.list();
  } else if (payload === initPayload.productList) {
    var { data } = await productModel.list();
  } else if (payload === initPayload.productColorList) {
    var { data } = await productColorModel.list();
  } else if (payload === initPayload.productDirectionList) {
    var { data } = await productDirectionModel.list();
  } else if (payload === initPayload.productLightList) {
    var { data } = await productLightModel.list();
  } else if (payload === initPayload.orderList) {
    var { data } = await orderModel.list();
  }
  
  dispatch(initList({ attr: payload, list: data.msg }));
};
export const asyncAddList =
  (payload: object, attr: string) => async (dispatch: any) => {
    if (attr === initPayload.partaList) {
      var { data } = await partaModel.add(payload);
    } else if (attr === initPayload.partbList) {
      var { data } = await partbModel.add(payload);
    } else if (attr === initPayload.productList) {
      var { data } = await productModel.add(payload);
    } else if (attr === initPayload.productColorList) {
      var { data } = await productColorModel.add(payload);
    } else if (attr === initPayload.productDirectionList) {
      var { data } = await productDirectionModel.add(payload);
    } else if (attr === initPayload.productLightList) {
      var { data } = await productLightModel.add(payload);
    } else if (attr === initPayload.orderList) {
      await orderModel.addall(payload);
      return false
    }
    dispatch(addList({ attr, item: data.data[0] }));
  };
export const asyncDelList =
  (payload: string, attr: string) => async (dispatch: any) => {
    if (attr === initPayload.partaList) {
      await partaModel.del({ _id: payload });
    } else if (attr === initPayload.partbList) {
      await partbModel.del({ _id: payload });
    } else if (attr === initPayload.productList) {
      await productModel.del({ _id: payload });
    } else if (attr === initPayload.productColorList) {
      await productColorModel.del({ _id: payload });
    } else if (attr === initPayload.productDirectionList) {
      await productDirectionModel.del({ _id: payload });
    } else if (attr === initPayload.productLightList) {
      await productLightModel.del({ _id: payload });
    } else if (attr === initPayload.orderList) {
      await orderModel.del({ _id: payload });
    }

    dispatch(delList({ attr, _id: payload }));
  };
