import React from "react";
import { Button, Form, Input,message } from 'antd';
import { useHistory} from "react-router-dom"
import loginModel from "../model/loginModel"
export default function Login() {
  let history = useHistory()
  
  const onFinish = async (values: any) => {
    let {data} = await loginModel(values.username,values.userpwd)
    if(data.code===1){
      message.success('');
      history.push("home")
    }else{
      message.error('!  !');
    }
  };

  const onFinishFailed = (errorInfo: any) => {
    console.log('Failed:', errorInfo);
  };
  return (
    <div
      style={{
        width: "100%",
        height: "100%",
        position: "relative",
        backgroundColor: "#3e9dfd",
      }}
    >
      <div
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          margin: "auto",
          backgroundColor: "#fff",
          maxWidth: "800px",
          maxHeight: "500px",
          boxShadow: "20px 10px 30px 10px rgba(0,0,0,0.2)",
          borderRadius: "10px",
        }}
      >
        <div style={{ fontSize: "30px", color: "#999", textAlign: "center" ,margin:"60px 0"}}>
          27
        </div>
        <div style={{width:"50%",margin:"0 auto"}}>
        <Form
          name="basic"
          labelCol={{ span: 4 }}
          wrapperCol={{ span: 20 }}
          initialValues={{ remember: true }}
          onFinish={onFinish}
          // onFinishFailed={onFinishFailed}
          autoComplete="off"
        >
          <Form.Item
            label=""
            name="username"
            rules={[{ required: true, message: "" }]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            label=""
            name="userpwd"
            rules={[{ required: true, message: "" }]}
          >
            <Input.Password />
          </Form.Item>

          {/* <Form.Item
            name="remember"
            valuePropName="checked"
            wrapperCol={{ offset: 8, span: 16 }}
          >
            <Checkbox>Remember me</Checkbox>
          </Form.Item> */}

          <Form.Item wrapperCol={{span: 14, offset: 6  }} style={{marginTop:"50px"}}>
            <Button type="primary"  htmlType="submit" block>
              
            </Button>
          </Form.Item>
        </Form>
        </div>
      </div>
    </div>
  );
}
