import React from "react";
import {
  HashRouter as Router,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";
import NotFound from "../view/NotFound";
import Login from "../view/Login";
import Home from "../view/Home"
import Print from "../view/Print";
export default function RouterView() {
  let list = [
    {id:1,path:"/",com:Login,exact:true},
    {id:2,path:"/home",com:Home},
    {id:3,path:"/print",com:Print}
  ];
  return (
    <Router>
      <Switch>
        {
            list.map(item=>{
                return <Route key={item.id} path={item.path} component={item.com} exact={item.exact}></Route>
            })
        }
        <Route path="/notfound" component={NotFound}></Route>
        <Redirect path="*" to="/notfound"></Redirect>
      </Switch>
    </Router>
  );
}
