import React,{lazy,Suspense} from 'react'
import { Route, Switch,Redirect} from "react-router-dom";
import {
    LaptopOutlined,
    NotificationOutlined,
    UserOutlined,
} from "@ant-design/icons";
let Parta = lazy(() => import("../view/Parta"));
let Partb = lazy(() => import("../view/Partb"));
let Product = lazy(()=>import("../view/Product"))
let ProductColor = lazy(()=>import("../view/ProductColor"))
let ProductDirection = lazy(()=>import("../view/ProductDirection"))
let ProductLight = lazy(()=>import("../view/ProductLight"))
let Order = lazy(()=>import("../view/Order"))
let OrderList = lazy(()=>import("../view/OrderList"))
export const rList = [
  { id: 1, path: "/parta",msg:"", com: Parta ,icon:UserOutlined,},
  { id: 2, path: "/partb",msg:"", com: Partb ,icon:LaptopOutlined},
  { id: 3, path: "/product",msg:"", com: Product ,icon:NotificationOutlined},
  { id: 4, path: "/productcolor",msg:"" ,com: ProductColor ,icon:LaptopOutlined},
  { id: 5, path: "/productdirection",msg:"", com: ProductDirection ,icon:LaptopOutlined},
  { id: 6, path: "/productlight",msg:"", com: ProductLight ,icon:LaptopOutlined},
  { id: 7, path: "/order",msg:"", com: Order ,icon:LaptopOutlined},
  { id: 8, path: "/orderlist",msg:"", com: OrderList ,icon:LaptopOutlined},
];
export default function RouterViewHome() {
  return (
    <Suspense fallback={<div>Loading...</div>}>
      <Switch>
        {
          rList.map(item=>{
            return <Route path={"/home" +item.path} component={item.com} key={item.id}/>
            })
        }
        <Redirect path='/home' to="/home/parta"></Redirect>
      </Switch>
    </Suspense>
  )
}
